.menu {
  text-transform: uppercase;
}

.menu__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.menu__list > li {
  position: relative;
  transition: all .1s ease;
}

.menu__list > li:not(:last-child) {
  margin-right: 24px;
}

.menu__list > li::after {
  position: absolute;
  bottom: -4px;
  left: 0;
  display: block;
  content: '';
  width: 100%;
  height: 2px;
  background-color: #3A82E0;
  opacity: 0;
  transition: all .1s ease;
}

.menu__list > li:hover {
  color: #3A82E0;
}

.menu__list > li:hover::after {
  opacity: 1;
}

.menu__list > li:hover .menu__submenu-wrapper {
  visibility: visible;
  opacity: 1;
}

.menu__list > li.active {
  color: #3A82E0;
}

.menu__list > li.active::after {
  opacity: 1;
}

.menu__list a {
  text-decoration: none;
  color: inherit;
  outline: none;
  position: relative;
  z-index: 2;
  display: inline-block;
  height: 100%;
}

.menu__submenu-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 200px;
  color: #1C3E53;
  text-transform: none;
  padding-top: 24px;
  margin-left: -20px;
  visibility: hidden;
  opacity: 0;
}

.menu__submenu {
  padding: 0;
  margin: 0;
  list-style-type: none;
  background-color: #fff;
  box-shadow: 0px 0px 20px rgba(38, 55, 61, 0.07);
  padding: 20px 20px;
}

.menu__submenu li {
  transition: all .1s ease;
}

.menu__submenu li:not(:last-child) {
  margin-bottom: 10px;
}

.menu__submenu li:hover {
  color: #3A82E0;
}

.menu-footer {
  color: #fff;
}

.menu-footer__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.menu-footer__list a {
  margin-top: 16px;
  transition: color .1s ease;
}

.menu-footer__list a:hover {
  color: #a0d7ff;
}

.menu-footer__submenu-wrapper {
  margin-top: 24px;
}

.menu-footer__submenu {
  padding: 0;
  margin: 0;
  list-style-type: none;
  font-size: 12px;
  line-height: 16px;
}

.menu-footer__submenu li {
  margin-top: 16px;
}

.menu-footer a {
  text-decoration: none;
  color: inherit;
  outline: none;
}

