html, body {
  font-family: 'Lato', sans-serif;
  margin: 0;
  padding: 0;
  text-size-adjust: none;
  font-size: 18px;
  line-height: 24px;
  background-color: #fff;
  color: #1C3E53;
  position: relative;
}

* {
  box-sizing: border-box;
}

img {
  border: none;
  max-width: 100%;
}

button {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

button:focus, button:active {
  outline: 0px solid transparent;
}

picture {
  display: block;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input.error {
  box-shadow: 0 0 2px 1px red;
}

#workemail {
  display: none;
}

.container {
  margin: 0 auto;
  width: 100%;
  min-width: 320px;
  max-width: 1250px;
  padding: 0 40px;
}

.wrapper {
  min-height: 100vh;
  min-height: calc(var(--vh) * 100);
  display: flex;
  flex-direction: column;
}

.content {
  position: relative;
  z-index: 1;
}

.content__section {
  margin-top: 48px;
}

.title {
  font-size: 24px;
  line-height: 32px;
  margin-top: 0;
  margin-bottom: 24px;
}

.text-block p:not([class]) {
  margin: 0 0 16px 0;
}

.text-block ul:not([class]) {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.required::after {
  content: '*';
  color: #EB5757;
}

.select-language {
  position: relative;
  color: #3A82E0;
}

.select-language__title {
  border: none;
  background-color: transparent;
  outline: 0px solid transparent;
  padding: 0;
  margin: 0;
  cursor: pointer;
  font-size: inherit;
  color: inherit;
  font: inherit;
  border-radius: 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.select-language__title::before {
  display: block;
  width: 16px;
  height: 16px;
  content: '';
  background: url("../images/ru.svg") center no-repeat;
  margin-right: 8px;
}

.select-language__title::after {
  display: block;
  width: 12px;
  height: 16px;
  content: '';
  background: url("../images/arrow-down.svg") center no-repeat;
  margin-left: 8px;
}

.select-language__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  position: absolute;
  top: 20px;
  right: 0;
  left: 16px;
  min-width: 100px;
  white-space: nowrap;
  background-color: #fff;
  border: 1px solid #3A82E0;
  padding: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity .1s ease;
}

.select-language__list.active {
  opacity: 1;
  visibility: visible;
}

.select-language__item {
  padding: 4px 6px;
  cursor: pointer;
  transition: color .1s ease, background-color .1s ease;
}

.select-language__item:hover {
  background-color: #EBF7FF;
  color: #1C3E53;
}

