  }
}

@media screen and (max-width: 1220px) {
  .container {
    max-width: 992px;
  }
}

@media screen and (max-width: 1012px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (max-width: 788px) {
  .container {
    max-width: 576px;
  }
}

@media screen and (max-width: 596px) {
  .container {
    max-width: 100%;
    padding: 0 20px;
  }
}

